@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
	.animation-delay-2000 {
		animation-delay: 2s;
	}
	.animation-delay-4000 {
		animation-delay: 4s;
	}
}

* {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media only screen and (min-width: 600px) {
	.title-height {
		line-height: 3.7rem !important;
	}
}

.project {
	@apply absolute inset-0 hidden;
}

.project-content {
	@apply h-full m-1 p-2;
}

.project-title {
	flex: 0.2;
}

.project-body {
	flex: 1;
	margin: 1rem 0;
}

.project-body span {
	margin: 1rem 0;
	display: inline-block;
}

.tag {
	padding: 0.4rem 0.8rem;
	border-radius: 7px;
}

.project-footer {
	flex: 0;
}

.project-footer a {
	padding: 0.5rem 1rem;
	background: var(--red);
	border-radius: 7px;
	margin-right: 1rem;
	display: inline-block;
}

.project-footer svg {
	margin-left: 1rem;
}

/* Controls */

.control {
	margin-top: 1rem;
}

.control button {
	background: transparent;
	border: none;
	font-size: 3rem;
	color: var(--white);
}

.prev {
	transform: rotate(180deg);
	cursor: pointer;
	margin-right: 3rem;
	margin-top: 0.3rem;
}

.next {
	margin-left: 3rem;
	cursor: pointer;
}
